import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CarestaffCommissionComponent } from './carestaff-commission.component';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { MaterialModule } from 'src/app/dealer/material.module';
import { AddCarestaffCommissionComponent } from './add-carestaff-commission/add-carestaff-commission.component';
import { EditCarestaffCommissionComponent } from './edit-carestaff-commission/edit-carestaff-commission.component';

const routes: Routes = [
  {
    path: '',
    component: CarestaffCommissionComponent,
  },
];

@NgModule({
  declarations: [CarestaffCommissionComponent, AddCarestaffCommissionComponent, EditCarestaffCommissionComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    MaterialModule,
    FormsModule,
    RouterModule.forChild(routes),
  ]
})
export class CarestaffCommissionModule { }
