import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
// import { QueryVhweb, OrdinaryAlgorithm, FirebaseAuth, OrdinaryComponent } from 'private-angular-vhframeworks';
import { AddCarestaffCommissionComponent } from './add-carestaff-commission/add-carestaff-commission.component';
import { EditCarestaffCommissionComponent } from './edit-carestaff-commission/edit-carestaff-commission.component';

@Component({
  selector: 'app-carestaff-commission',
  templateUrl: './carestaff-commission.component.html',
  styleUrls: ['./carestaff-commission.component.scss']
})
export class CarestaffCommissionComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  /// panigatior ///
  loading: boolean = false;
  pageIndex: number = 0;
  pageSize: number = 10;
  lowValue: number = 0;
  highValue: number = 10;
  pageSizeOption: any = [10, 25, 50, 100];
  careStaffCommission:any =[]
  constructor(
    // public queryVhweb:QueryVhweb,
    // public ordinaryAlgorithm:OrdinaryAlgorithm,
    public dialog: MatDialog,
    // public firebaseAuth: FirebaseAuth,
    // public ordinaryComponent: OrdinaryComponent,
  ) { }

  ngOnInit(): void {
    this.loading =true
    this.getCommission_CareStaff_Payments()
  }

  getCommission_CareStaff_Payments(){
    // this.queryVhweb.getCommission_CareStaff_Payments()
    // .then((data)=>{
    //   this.careStaffCommission = this.ordinaryAlgorithm.sortbyASC(data,'name') 
    //   this.loading =false
    //   console.log(data)
    // }, err=> {
    //   this.loading =false
    //   console.log(err)
    // })
  }

  openAddCommission(){
    const dialogRef = this.dialog.open(AddCarestaffCommissionComponent, {
      width: '80%',
      maxWidth: '100%',
      disableClose:true
    });

    dialogRef.afterClosed().subscribe((result) => {
      if(result) this.getCommission_CareStaff_Payments();
    });
  }
  openEditCommission(item){
    const dialogRef = this.dialog.open(EditCarestaffCommissionComponent, {
      width: '80%',
      maxWidth: '100%',
      disableClose:true,
      data:item
    });

    dialogRef.afterClosed().subscribe((result) => {
      if(result) this.getCommission_CareStaff_Payments();
    });
  }
// slice panigator
getPaginatorData(event: PageEvent): PageEvent {
  this.lowValue = event.pageIndex * event.pageSize;
  this.highValue = this.lowValue + event.pageSize;
  return event;
}

}
